.Project {
  width: 330px;
  height: 190px;
  position: relative;
  text-align: center;
  overflow: hidden;
  padding: 0px;
  /* border-radius: 10px; */
  margin: 20px;
  align-self: flex-start;
  font-family: "Neucha", cursive;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.7);
  transition: 0.3s;
  /* border: 2px solid rgb(51,51,51); */
}
.Project:hover {
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.7);
}

.Project img {
  width: 100%;
  height: 100%;
}

.Project p {
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: 0px;
  display: block;
  padding: 10px;
  padding-block: 12px;
  background-color: rgba(51, 51, 51, 0.7);
  color: rgb(245, 245, 245);
  margin: 0px 0px;
  font-weight: 500;
}

.Project:hover {
}

@media only screen and (max-width: 700px) {
  .Project {
    width: 300px;
    height: 180px;
    margin: 15px;
  }
}
.project-type-class {
  font-size: 0.8rem;
  font-weight: 500;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: #f5f5f5;
  width: 50px;
  height: 50px;
  text-align: center;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: rotate(-30deg);
  padding: 5px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.7);
  transition: 0.3s;
}
